$veryDarkDesaturatedBlue: hsl(238, 29%, 16%); //text primary
$softRed: hsl(14, 88%, 65%); //text primary


$softViolet: hsl(273, 75%, 66%); //bg gradient
$softBlue: hsl(240, 73%, 65%); //bg gradient

$veryDarkGrayishBlue: hsl(237, 12%, 33%); //text neutral
$darkGrayishBlue: hsl(240, 6%, 50%); //text neutral

$lightGrayishBlue: hsl(240, 5%, 91%); //dividers

$white: hsl(0, 100%, 100%); //card


body{
    background: linear-gradient(90deg, $softViolet, $softBlue);
}

#centre-card{
    position: absolute;
    left: 50%;
    top: calc(50% + 32px);
    transform: translate(-50%, -50%);
    width: 327px;
    height: 537px;
    background-color: $white;
    border-radius: 20px;
    text-align: center;
    font-family: 'Kumbh Sans', sans-serif;
    

    img{
        width: 237px;
        margin: -107px auto auto auto;
        display: flex;
    }

    h1{
        font-weight: 700;
        color: $veryDarkDesaturatedBlue;
        margin-top: 57px;
    }
}

#question-area{
    margin: -7px 20px auto 20px;
}



.question{
    cursor: pointer;
    text-align: left;
    border: none;
    transition: 200ms;
    width: 280px;
    margin: auto auto auto auto;
    padding: 17px 0px 16px 1px;
    border-bottom: 1px solid $lightGrayishBlue;
    font-size: 9.7pt;
    color: $veryDarkGrayishBlue;
    position: relative;
    display: block;

    background: $white url(images/icon-arrow-down.svg);
    background-position: 270px 25px;
    background-repeat: no-repeat;

    &:hover{
        color: $softRed;
    }
}


.answer{
    p{
        color: $darkGrayishBlue;
        display: none;
        font-size: 9pt;

    }
}




.attribution{
    font-family: 'Kumbh Sans', sans-serif;
    font-size: 8pt;
    position: absolute;
    right: 0px;
    bottom: 0px;
}